import React, { useEffect, useState } from 'react';
import { useDispatch, useSelector } from 'react-redux';

import { getKecamatan, wilayah, getData } from '../actions/cariWilayah';

import {
  Select,
  TextField,
  FormControl,
  InputLabel,
  Button,
  Table,
  TableBody,
  TableCell,
  TableContainer,
  TableHead,
  TableRow,
  Paper,
} from '@material-ui/core';
import { Autocomplete, Pagination } from '@material-ui/lab';
import { makeStyles } from '@material-ui/core/styles';

const useStyles = makeStyles({
  option: {
    fontSize: 15,
    '& > span': {
      marginRight: 10,
      fontSize: 18,
    },
  },
  table: {
    minWidth: 650,
  },
  formControl: {
    display: 'flex',
    flexDirection: 'row',
    width: 100,
  },
  comboContainer: {
    display: 'flex',
    width: '100%',
    flex: '0 0 100%',
    marginTop: '10px',
    paddingRight: '10px',
  },
});

const CariWilayah = () => {
  const dispatch = useDispatch();

  const classes = useStyles();

  const [state, setState] = useState({
    searchBy: 'NIK',
    keyword: '',
  });

  const [kec, setKec] = useState('');
  const [kel, setKel] = useState('');
  const [tgl, setTgl] = useState('');

  const [currentPage, setCurrentPage] = useState(1);
  const [postsPerPage] = useState(10);

  const { kecamatan, kelurahan, isDisabled } = useSelector(
    (state) => state.wilayah
  );

  const { keluarga = [] } = useSelector((state) => state.dukcapil);

  useEffect(() => {
    dispatch(getKecamatan());
  }, [dispatch]);

  // Get current posts
  const indexOfLastPost = currentPage * postsPerPage;
  const indexOfFirstPost = indexOfLastPost - postsPerPage;
  const currentPosts = keluarga.slice(indexOfFirstPost, indexOfLastPost);

  const handleChange = (event) => {
    const name = event.target.name;
    setState({
      ...state,
      [name]: event.target.value,
    });
    setTgl('');
  };

  const handlePageChange = (event, value) => {
    setCurrentPage(value);
  };

  const selectHandler = (kec) => {
    dispatch(wilayah(kec));
  };

  const ret = { [state.searchBy]: state.keyword };
  if (kel) ret.NO_KEL = kel.kodeKel;
  if (kec) ret.NO_KEC = kec.kodeKec;
  if (tgl) ret.TGL_LHR = tgl;

  const submitHandler = (e) => {
    e.preventDefault();
    setCurrentPage(1)
    if (!state.keyword) {
      return;
    }
    dispatch(getData(JSON.stringify(ret)));
  };

  return (
    <>
      <div className="container">
        <form
          noValidate
          autoComplete="off"
          onSubmit={submitHandler}
          encType="application/json"
        >
          <div className={classes.comboContainer}>
            <Autocomplete
              id="pilih-kec"
              style={{
                display: 'flex',
                justifyContent: 'flex-start',
                flexDirection: 'column',
                flex: '0 0 100%',
                paddingRight: '10px',
              }}
              options={kecamatan}
              classes={{
                option: classes.option,
              }}
              onChange={(event, value) => {
                selectHandler(value);
                setKec(value);
                setKel('');
              }}
              autoHighlight
              getOptionLabel={(option) => option.NamaKec}
              renderOption={(option) => (
                <React.Fragment>{option.NamaKec}</React.Fragment>
              )}
              renderInput={(params) => {
                return (
                  <TextField
                    {...params}
                    label="Pilih Kecamatan"
                    variant="outlined"
                    inputProps={{
                      ...params.inputProps,
                      autoComplete: 'off', // disable autocomplete and autofill
                    }}
                  />
                );
              }}
            />
          </div>
          <div className={classes.comboContainer}>
            <Autocomplete
              id="pilih-kec"
              style={{
                display: 'flex',
                justifyContent: 'flex-start',
                flexDirection: 'column',
                flex: '0 0 100%',
                paddingRight: '10px',
              }}
              disabled={isDisabled}
              options={kelurahan}
              classes={{
                option: classes.option,
              }}
              onChange={(event, value) => {
                setKel(value);
              }}
              autoHighlight
              getOptionLabel={(option) => option.label}
              // getOptionSelected={(option) => kec ? option.label : option[0]}
              renderOption={(option) => (
                <React.Fragment>{option.label}</React.Fragment>
              )}
              renderInput={(params) => {
                return (
                  <TextField
                    {...params}
                    label="Pilih Kelurahan"
                    variant="outlined"
                    inputProps={{
                      ...params.inputProps,
                      autoComplete: 'off', // disable autocomplete and autofill
                    }}
                  />
                );
              }}
            />
          </div>
          <FormControl variant="outlined" className={classes.comboContainer}>
            <InputLabel htmlFor="outlined-age-native-simple">
              Keyword
            </InputLabel>
            <Select
              native
              value={state.searchBy}
              onChange={handleChange}
              label="Keyword"
              inputProps={{
                name: 'searchBy',
                id: 'outlined-age-native-simple',
              }}
            >
              <option value={'NIK'}>NIK</option>
              <option value={'NO_KK'}>NO KK</option>
              <option value={'NAMA_LGKP'}>Nama</option>
            </Select>
          </FormControl>
          <TextField
            id="outlined-basic"
            style={{ width: '100%', marginTop: '10px', marginBottom: '10px' }}
            name="keyword"
            label="SEARCH"
            variant="outlined"
            value={state.keyword}
            onChange={handleChange}
          />
          {state.searchBy === 'NAMA_LGKP' && (
            <TextField
              id="date"
              label="Tanggal Lahir"
              type="date"
              defaultValue="2017-05-24"
              onChange={(e) => setTgl(e.target.value)}
              InputLabelProps={{
                shrink: true,
              }}
            />
          )}
          <div className={classes.comboContainer}>
            <Button type="submit" variant="contained" color="primary">
              Cari Dukcapil
            </Button>
            <Button variant="contained">Log Aktivitas</Button>
          </div>
        </form>
      </div>
      <div className="table-container">
        <TableContainer component={Paper}>
          <Table className={classes.table} aria-label="simple table">
            <TableHead>
              <TableRow>
                <TableCell>No KK</TableCell>
                <TableCell>NIK</TableCell>
                <TableCell>Nama Lengkap</TableCell>
                <TableCell align="right">Tanggal Lahir</TableCell>
                <TableCell align="right">Hubungan Keluarga</TableCell>
                <TableCell>Nama Ibu</TableCell>
                <TableCell>Nama Ayah</TableCell>
                <TableCell align="right">Kelurahan</TableCell>
              </TableRow>
            </TableHead>
            <TableBody>
              {currentPosts &&
                currentPosts.map((kk) => (
                  <TableRow key={kk.NIK}>
                    <TableCell component="th" scope="row">
                      {kk.NO_KK}
                    </TableCell>
                    <TableCell>{kk.NIK}</TableCell>
                    <TableCell>{kk.NAMA_LGKP}</TableCell>
                    <TableCell align="center">{kk.TGL_LHR}</TableCell>
                    <TableCell align="center">{kk.STAT_HBKEL}</TableCell>
                    <TableCell>{kk.NAMA_LGKP_IBU}</TableCell>
                    <TableCell>{kk.NAMA_LGKP_AYAH}</TableCell>
                    <TableCell align="right">{kk.NO_KEL}</TableCell>
                  </TableRow>
                ))}
            </TableBody>
          </Table>
        </TableContainer>
      </div>
      {keluarga.length / postsPerPage > 1 && (
        <div className="pagination">
          <Pagination
            count={Number(keluarga.length / postsPerPage)}
            page={currentPage}
            onChange={handlePageChange}
          />
        </div>
      )}
    </>
  );
};

export default CariWilayah;
