import {
  CARI_KELURAHAN,
  GET_KECAMATAN,
  CLEAR_WILAYAH,
  DATA_REQUEST,
  DATA_SUCCESS,
  GET_DATA_ERROR,
} from '../constans/dukcapil.Types';

export const cari = (state = { kecamatan: {} }, action) => {
  switch (action.type) {
    case GET_KECAMATAN:
      return {
        ...state,
        kecamatan: action.payload,
        isDisabled: true,
      };
    case CARI_KELURAHAN:
      return {
        ...state,
        kelurahan: action.payload,
        isDisabled: false,
      };
    case CLEAR_WILAYAH:
      return {
        ...state,
        isDisabled: true,
        kelurahan: ['Pilih'],
      };
    default:
      return state;
  }
};

export const getDataDukcapil = (state = {}, action) => {
  switch (action.type) {
    case DATA_REQUEST:
      return {
        ...state,
        isLoading: true,
      };
    case DATA_SUCCESS:
      return {
        ...state,
        keluarga: action.payload,
        isLoading: false,
      };
    case GET_DATA_ERROR:
      return {
        ...state,
        isLoading: false,
        error: action.payload,
      };
    default:
      return state;
  }
};
