import axios from 'axios';

import {
  CARI_KELURAHAN,
  GET_KECAMATAN,
  CLEAR_WILAYAH,
  DATA_REQUEST,
  DATA_SUCCESS,
  GET_DATA_ERROR,
} from '../constans/dukcapil.Types';

import { WILAYAH } from '../data/DATAWILAYAH';

export const wilayah = (kec) => async (dispatch) => {
  if (!kec) {
    dispatch({
      type: CLEAR_WILAYAH,
    });
    return;
  }
  const pilihanKec = WILAYAH.filter(
    (i) => i.kodeKec.toString() === kec.kodeKec.toString()
  );

  const pilihanKel = pilihanKec.map((kel) => kel.kelurahan);
  dispatch({
    type: CARI_KELURAHAN,
    payload: pilihanKel[0],
  });
};

export const getKecamatan = () => async (dispatch) => {
  const kecamatan = WILAYAH.map((kec) => ({
    kodeKec: kec.kodeKec,
    NamaKec: kec.kecamatan,
  }));

  dispatch({
    type: GET_KECAMATAN,
    payload: kecamatan,
  });
};

export const getData = (formData) => async (dispatch) => {
  try {
    console.log(formData);
    dispatch({ type: DATA_REQUEST });

    const config = {
      headers: {
        'Content-Type': 'application/json',
      },
    };

    const { data } = await axios.post('http://localhost:3001/api/v1/dukcapil', formData, config);
    dispatch({
      type: DATA_SUCCESS,
      payload: data,
    });
  } catch (error) {
    dispatch({
      type: GET_DATA_ERROR,
      payload: error.response.data.message,
    });
  }
};
